import React, { useState } from 'react'
import './comp-styles.css'


export default function Entry({id,task,del,edi}) {

    const [state,setState] = useState({pDisp:'block',iDisp:'none'})
    const [input, setInput] = useState("");


    var deleteHandler = ()=>{
        del(task);
    }
    var editHandler = ()=>{
        setState({pDisp:'none',iDisp:'block'})
    }

    var updateHandler = ()=>{
        edi(id,input);
        setState({pDisp:'block',iDisp:'none'});
    }

    var handleChange = (e)=>{
        setInput(e.target.value);
    }
    
    return (
        <div className="entry">
            <p style={{display:state.pDisp}}>{(id+1).toString()+") "+task}</p>
            <input value = {input} style={{display:state.iDisp}} type="text" onChange={handleChange}/>
            <button className="edit" style={{display:state.pDisp}} onClick={editHandler}> edit </button>
            <button className="update" style={{display:state.iDisp}} onClick={updateHandler}> Update </button>
            <button className="delete" style={{display:state.pDisp}} onClick={deleteHandler}> X </button>
        </div>
    )
}
