import React, { useEffect, useState } from 'react'
import Entry from './Entry'
import './comp-styles.css'


export default function TodoList() {
    const [input, setInput] = useState("");
    const [tasks,setTasks] = useState([]);
    const [empty,setEmpty] = useState(0.0);

  function change(arr) {
    setTasks(arr);
  }

  var createHandler = ()=>{
    let temp = tasks;
    temp.push(input);
    change(temp);
    setEmpty(Math.random());
    setInput("");

}
var handleChange = (e)=>{
    setInput(e.target.value);
    setEmpty(Math.random());
}


  function del(ele){
    var a = tasks;
    var ind = tasks.indexOf(ele);
    if(ind > -1) a.splice(ind,1);
    var b= a;
    change(b);    
    setEmpty(Math.random());
  }

  function edit(id,ele){
      var a = tasks;
      a.splice(id,1,ele);
      
    change(a);    
    setEmpty(Math.random());

  }


   
   

    const listItems = tasks.map((e,i) =>
    <Entry task={e} id={i} key={i} del = {del} edi = {edit}/>);


    
    return (
        <div className='todo-list'>
            <h2>List of Items</h2> 
            
            {listItems}
            <div style={{display:'flex',background:'transparent'}}>
            <input value={input} onChange = {handleChange} type="text" />
            <button onClick={createHandler}> Add </button>
            </div>
            
            
        </div>
    )
}
